body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 1280px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.NavBar ul {
	list-style-type: none;
	padding: 0;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin: 50px auto 0 auto;

	font-size: 40px;
}

.NavBar ul li {
	display: inline;
	margin-right: 30px;
}

.NavBar ul li a {
	text-decoration: none;
	font-weight: bold;
	padding: 10px 20px;
	border-radius: 44px;

	color: cornflowerblue;
}

.NavBar ul li a:hover {
	background-color: cornflowerblue;

	color: white;
}
.home{
    display: grid;
    grid-template-columns: 2fr 3fr;
    align-items: center;
}

.home .home-content h1 {
    font-size: 7vw;
    margin-top: 0;
}

.home .home-content h2 {
    font-weight: lighter;
    margin-bottom: 0;
    /*font-size: 1.3vw;*/
}

.home-content .contact {
    margin-top: 50px;
}

.home > img {
    width: 100%;
}

@media all and (max-width: 1024px) {
    .home .home-content h1 {
        font-size: calc(70px + 2vw);
    }
}
.contact a {
    margin-right: 20px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.contact img {
    height: 50px;
}
.about > h1 {
    font-size: 60px;
    margin: 100px auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.blue-color {
    color: cornflowerblue;
}

.about-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 3vw;
}

.about-content > img {
    width: 100%;
    max-width: 560px;
    height: auto;
    align-self: center;
}

.github-resume {
    margin: 50px auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.github-resume a {
    padding: 10px 30px;
    margin-right: 100px;
    border-radius: 15px;
    box-shadow: 0 5px 0 grey;

    font-size: 17px;
    font-weight: bold;
    text-decoration: none;
    color: dodgerblue;
    background-color: #e5f4ff;
}

@media all and (max-width: 1024px) {
    .about-content {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto auto;
    }

    .about-content img {
        justify-self: center;
        margin-bottom: 50px;
    }

    .github-resume a {
        font-size: 25px;
    }
}
.about-descriptions p {
    text-align: justify;
    font-size: 15px;
    display: block;
}

.about-descriptions p > a {
    text-decoration: none;
    font-weight: bold;
}

.about-descriptions h2 {
    font-size: 20px;
    margin-top: 40px;
}

.about-descriptions ul {
    list-style-type: none;

    padding: 0;
}

.about-descriptions ul li {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: inline-block;

    padding: 10px 20px;
    margin-right: 10px;
    margin-bottom: 10px;
    background-color: #1f5673;
    border-radius: 5px;

    color: white;
}

@media all and (max-width: 1024px){
    .about-descriptions p {
        font-size: 25px;
    }

    .about-descriptions ul li {
        font-size: 25px;
    }
}
