.experience > h1 {
	font-size: 60px;
	margin: 100px auto;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}
.experience-card {
	padding: 2vw 5vw;
	margin-top: 80px;

	border-radius: 20px;
	box-shadow: 1px 1px 15px 0px grey;

	background-color: white;
}

.experience-card .title-employer {
	display: flex;
	flex-direction: row;
}

.experience-card .title-employer h1 {
	margin-right: 20px;
}

.experience-card .time-place {
	color: grey;
	font-weight: bold;
	font-size: 23px;

	border-bottom: 1px solid black;
}

.experience-card .time-place p {
	display: inline-block;
}

.experience-card .time-place .place {
	float: right;
}

.experience-card .detail-list {
	list-style-type: none;
	padding: 0 20px;
}

.experience-card .detail-list li {
	margin-top: 20px;
	font-size: 20px;
}

.detail-list li:before {
	content: "";
	border-color: transparent cornflowerblue;
	border-style: solid;
	border-width: 0.35em 0 0.35em 0.45em;
	display: block;
	height: 0;
	width: 0;

	left: -1em;
	top: 1.05em;
	position: relative;
}
.project {
	margin-top: 73px;

	display: flex;
	flex-direction: column;
}

.title {
	font-size: 70px;
	text-align: center;
}

body {
	font-family: -apple-system,BlinkMacSystemFont,Omnes,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,
	Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
}

.body {
	background-color: #f9fafc;
	display: grid;
	grid-template-columns: 1fr 5fr 1fr;
}

.content {
	grid-column: 2 / 3;
	display: grid;
	grid-row-gap: 100px;
}


